:root {
  --blue-accent: #00acdc;
  --black: #000;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.header {
  justify-content: space-between;
  align-items: center;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.nav__list {
  margin: 0;
  padding: 23px 0;
  list-style: none;
  display: flex;
}

.img {
  width: 40px;
  height: 40px;
  margin: 10px 0;
  display: block;
}

.nav__link {
  color: var(--black);
  padding: 23px 0;
  text-decoration: none;
  position: relative;
}

.nav__item {
  text-transform: uppercase;
  margin-right: 20px;
  font-size: 12px;
  font-weight: 500;
}

.nav__item:last-child {
  margin-right: 0;
}

.is-active, .nav__link:hover {
  color: var(--blue-accent);
}

.is-active:after {
  content: "";
  background-color: var(--blue-accent);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}
